import {
  Box,
  Button,
  Grid,
  Typography,
  TextField,
  FormControlLabel,
  Checkbox,
  RadioGroup,
  FormControl,
  Radio,
  Collapse,
} from "@mui/material";
import React from "react";
import { useDispatch } from "react-redux";
import { IncreaseStep, DecreaseStep } from "../../Services/actions/action";

const PaymentForm = () => {
  const dispatch = useDispatch();
  const [value, setValue] = React.useState("offline");
  const [open, setOpen] = React.useState(false);
  const handleChange = (event) => {
    setValue(event.target.value);
    localStorage.setItem("paidSource", value);
  };
  React.useEffect(() => {
    localStorage.setItem("paidSource", value);
    if (value === "offline") {
      setOpen(false);
    } else {
      setOpen(true);
    }
  }, [value]);
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Payment method
      </Typography>
      <FormControl>
        <RadioGroup
          aria-labelledby="demo-controlled-radio-buttons-group"
          name="controlled-radio-buttons-group"
          value={value}
          onChange={handleChange}
        >
          <FormControlLabel
            value="offline"
            control={<Radio />}
            label="Cash Payment"
          />
          <FormControlLabel
            disabled
            value="Online"
            control={<Radio />}
            label="Online Payment"
          />
          <Collapse in={open}>
            <Grid container spacing={3}>
              <Grid item xs={12} md={6}>
                <TextField
                  required
                  id="cardName"
                  label="Name on card"
                  fullWidth
                  autoComplete="cc-name"
                  variant="standard"
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  required
                  id="cardNumber"
                  label="Card number"
                  fullWidth
                  autoComplete="cc-number"
                  variant="standard"
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  required
                  id="expDate"
                  label="Expiry date"
                  fullWidth
                  autoComplete="cc-exp"
                  variant="standard"
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  required
                  id="cvv"
                  label="CVV"
                  helperText="Last three digits on signature strip"
                  fullWidth
                  autoComplete="cc-csc"
                  variant="standard"
                />
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={
                    <Checkbox color="secondary" name="saveCard" value="yes" />
                  }
                  label="Remember credit card details for next time"
                />
              </Grid>
            </Grid>
          </Collapse>
        </RadioGroup>
        <Grid item xs={12}>
          <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
            <Button
              sx={{ mt: 3, ml: 1 }}
              onClick={() => dispatch(DecreaseStep(1))}
            >
              Back
            </Button>

            <Button
              variant="contained"
              sx={{ mt: 3, ml: 1 }}
              onClick={() => dispatch(IncreaseStep(1))}
            >
              Next
            </Button>
          </Box>
        </Grid>
      </FormControl>
    </React.Fragment>
  );
};

export default PaymentForm;
